/* Add a cookie consent pop-up to inform users that we store cookies related to their theme preference */

.cc-window {
    -webkit-transition: opacity 1s ease;
            transition: opacity 1s ease;

    opacity: 1;
}

.cc-window.cc-invisible {
    opacity: 0;
}

.cc-animate.cc-revoke {
    -webkit-transition:         transform 1s ease;
    -webkit-transition: -webkit-transform 1s ease;
            transition: -webkit-transform 1s ease;
            transition:         transform 1s ease;
            transition:         transform 1s ease, -webkit-transform 1s ease;
}

.cc-animate.cc-revoke.cc-top {
    -webkit-transform: translateY(-2em);
            transform: translateY(-2em);
}

.cc-animate.cc-revoke.cc-bottom {
    -webkit-transform: translateY(2em);
            transform: translateY(2em);
}

.cc-animate.cc-revoke.cc-active.cc-top {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.cc-animate.cc-revoke.cc-active.cc-bottom {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.cc-revoke:hover {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.cc-grower {
    overflow: hidden;

    max-height: 0;

    -webkit-transition: max-height 1s;
            transition: max-height 1s;
}

.cc-revoke,
.cc-window {
    font-family: Helvetica, Calibri, Arial, sans-serif;
    font-size: 16px;
    line-height: 1.5em;

    position: fixed;
    z-index: 9999;

    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    overflow: hidden;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;

    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.cc-window.cc-static {
    position: static;
}

.cc-window.cc-floating {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
        flex-direction: column;

    max-width: 24em;
    padding: 2em;
}

.cc-window.cc-banner {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
        flex-direction: row;

    width: 100%;
    padding: 1em 1.8em;
}

.cc-revoke {
    padding: 0.5em;
}

.cc-revoke:hover {
    text-decoration: underline;
}

.cc-header {
    font-size: 18px;
    font-weight: 700;
}

.cc-btn,
.cc-close,
.cc-link,
.cc-revoke {
    cursor: pointer;
}

.cc-link {
    display: inline-block;

    padding: 0.2em;

    text-decoration: underline;

    opacity: 0.8;
}

.cc-link:hover {
    opacity: 1;
}

.cc-link:active,
.cc-link:visited {
    color: initial;
}

.cc-btn {
    font-size: 0.9em;
    font-weight: 700;

    display: block;

    padding: 0.4em 0.8em;

    text-align: center;
    white-space: nowrap;

    border-width: 2px;
    border-style: solid;
}

.cc-highlight .cc-btn:first-child {
    border-color: transparent;
    background-color: transparent;
}

.cc-highlight .cc-btn:first-child:focus,
.cc-highlight .cc-btn:first-child:hover {
    text-decoration: underline;

    background-color: transparent;
}

.cc-close {
    font-size: 1.6em;
    line-height: 0.75;

    position: absolute;
    top: 0.5em;
    right: 0.5em;

    display: block;

    opacity: 0.9;
}

.cc-close:focus,
.cc-close:hover {
    opacity: 1;
}

.cc-revoke.cc-top {
    top: 0;
    left: 3em;

    border-bottom-right-radius: 0.5em;
    border-bottom-left-radius: 0.5em;
}

.cc-revoke.cc-bottom {
    bottom: 0;
    left: 3em;

    border-top-left-radius: 0.5em;
    border-top-right-radius: 0.5em;
}

.cc-revoke.cc-left {
    right: unset;
    left: 3em;
}

.cc-revoke.cc-right {
    right: 3em;
    left: unset;
}

.cc-top {
    top: 1em;
}

.cc-left {
    left: 1em;
}

.cc-right {
    right: 1em;
}

.cc-bottom {
    bottom: 1em;
}

.cc-floating > .cc-link {
    margin-bottom: 1em;
}

.cc-floating .cc-message {
    display: block;

    margin-bottom: 1em;
}

.cc-window.cc-floating .cc-compliance {
    -webkit-box-flex: 1;
    -ms-flex: 1 0 auto;
        flex: 1 0 auto;
}

.cc-window.cc-banner {
    -webkit-box-align: center;
    align-items: center;
    -ms-flex-align: center;
}

.cc-banner.cc-top {
    top: 0;
    right: 0;
    left: 0;
}

.cc-banner.cc-bottom {
    right: 0;
    bottom: 0;
    left: 0;
}

.cc-banner .cc-message {
    display: block;
    -webkit-box-flex: 1;
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;

    max-width: 100%;
    margin-right: 1em;
}

.cc-compliance {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-line-pack: justify;
    align-content: space-between;
    -webkit-box-align: center;
    align-items: center;
    -ms-flex-align: center;
}

.cc-floating .cc-compliance > .cc-btn {
    -webkit-box-flex: 1;
    -ms-flex: 1;
        flex: 1;
}

.cc-btn + .cc-btn {
    margin-left: 0.5em;
}

@media print {
    .cc-revoke,
    .cc-window {
        display: none;
    }
}

@media screen and (max-width: 900px) {
    .cc-btn {
        white-space: normal;
    }
}

@media screen and (max-width: 414px) and (orientation: portrait),
screen and (max-width: 736px) and (orientation: landscape) {
    .cc-window.cc-top {
        top: 0;
    }

    .cc-window.cc-bottom {
        bottom: 0;
    }

    .cc-window.cc-banner,
    .cc-window.cc-floating,
    .cc-window.cc-left,
    .cc-window.cc-right {
        right: 0;
        left: 0;
    }

    .cc-window.cc-banner {
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    }

    .cc-window.cc-banner .cc-compliance {
        -webkit-box-flex: 1;
        -ms-flex: 1 1 auto;
            flex: 1 1 auto;
    }

    .cc-window.cc-floating {
        max-width: none;
    }

    .cc-window .cc-message {
        margin-bottom: 1em;
    }

    .cc-window.cc-banner {
        -webkit-box-align: unset;
        align-items: unset;
        -ms-flex-align: unset;
    }

    .cc-window.cc-banner .cc-message {
        margin-right: 0;
    }
}

.cc-floating.cc-theme-classic {
    padding: 1.2em;

    border-radius: 5px;
}

.cc-floating.cc-type-info.cc-theme-classic .cc-compliance {
    display: inline;
    -webkit-box-flex: 0;
    -ms-flex: none;
        flex: none;

    text-align: center;
}

.cc-theme-classic .cc-btn {
    border-radius: 5px;
}

.cc-theme-classic .cc-btn:last-child {
    min-width: 140px;
}

.cc-floating.cc-type-info.cc-theme-classic .cc-btn {
    display: inline-block;
}

.cc-theme-edgeless.cc-window {
    padding: 0;
}

.cc-floating.cc-theme-edgeless .cc-message {
    margin: 2em;
    margin-bottom: 1.5em;
}

.cc-banner.cc-theme-edgeless .cc-btn {
    height: 100%;
    margin: 0;
    padding: 0.8em 1.8em;
}

.cc-banner.cc-theme-edgeless .cc-message {
    margin-left: 1em;
}

.cc-floating.cc-theme-edgeless .cc-btn + .cc-btn {
    margin-left: 0;
}
